import React, { Component, Suspense } from 'react';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { COPY_TO_CLIPBOARD } from '../../../Services/Utils/utils';

class EmailCampaign extends Component {
    constructor() {
        super();
        this.state = { showModal:false, genrateCode: false, codeCopied: false };
    }
    EditCampaignUrlModal = event => this.setState({
        showModal: true
    })
    campaignUrlCallback = (campaignUrlEvent) => {
        this.setState({
            showModal: campaignUrlEvent
        })
    }
    codeCopy = (id) => {
        this.setState({
            codeCopied: true
        })
        setTimeout(() => {
            this.setState({
                codeCopied: false
            }) 
        }, 1500);
        COPY_TO_CLIPBOARD(id);
    }
    render() {
        const { genrateCode, showModal, codeCopied } = this.state
        return  <div className='d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100'>
                    
                </div>
    }
}

export default EmailCampaign;